<template>
  <div class="main">
    <el-container>
      <el-header>
        <div class="header">
          <div class="header_left">
            <div class="logoBox" @click="openHome">
              <img src="../assets/img/favicon.png">
              <h1>兜兜的导航</h1>
            </div>
            <div class="timeBox">
              <span>
                {{ realTime.Month }} <b>月</b> {{ realTime.day }} <b>日 {{ realTime.Hour }}:{{ realTime.Minute }}:{{
                  realTime.seconds }}</b>
              </span>
            </div>
          </div>
          <div class="header_right">
            <button>推荐网址</button>
          </div>
        </div>
      </el-header>
      <el-container>
        <el-aside width="200px">
          <el-scrollbar>
            <div class="menu">
              <div class="menuBoxBar" :style="{ transform: `translateY(${LineTop}px)` }"></div>
              <div :class="`menuBox ${index === menuActiverCode ? 'active' : ''}`" v-for="(item, index) in arrey"
                @click="active(index)">
                {{ item.title }}
              </div>
            </div>
          </el-scrollbar>
        </el-aside>
        <el-main>
          <el-scrollbar>
            <div class="container">
              <div class="container_box search">
                <div class="search_box">
                  <div class="header">
                    <h1>搜索</h1>
                  </div>
                  <div class="body">
                    <input type="text" v-model="searchValue" placeholder="我来帮你百度一下" @keyup.enter="search">
                    <button @click="search"><el-icon>
                        <Search />
                      </el-icon></button>
                  </div>
                </div>
              </div>
              <div class="container_box">
                <span style="color: #409eff;">开发中，敬请期待。</span> 
              </div>
            </div>
          </el-scrollbar>
        </el-main>
      </el-container>
    </el-container>
  </div>
</template>
<script setup>
import webData from '../untils/websiteData.js';
import { ref, onMounted, computed, reactive, nextTick, watchEffect, toRef, toRefs, watch, onUnmounted } from 'vue';
import { ElMessageBox, ElMessage, ElNotification } from 'element-plus'
import dayjs from 'dayjs';
function openHome() {
  window.open('/#/homeson/website')
}
const realTime = reactive({})
setInterval(() => {
  realTime.Month = dayjs().format('MM');
  realTime.day = dayjs().format('DD');
  realTime.Hour = dayjs().format('HH');
  realTime.Minute = dayjs().format('mm');
  realTime.seconds = dayjs().format('ss')
}, 100);
//#region 初始化数据

const arrey = webData.webArry

const recommendData = randomAcquisition(arrey)
function randomAcquisition(data) {
  let allData = data.reduce((e, item) => {
    return e = [...e, ...item.childers]
  }, [])
  const top20Items = allData.sort((a, b) => b.metaDataId - a.metaDataId).slice(0, 10)
  let newSort = {
    title: '近期更新',
    key: 'recommend',
    childers: top20Items
  }
  return newSort
}
if (arrey[0].title !== '近期更新') {
  arrey.unshift(recommendData)
}


//#endregion
//#region 左侧菜单选中效果
const menuActiverCode = ref(0)
const LineTop = ref(menuActiverCode.value * 30)
function active(index) {
  menuActiverCode.value = index
  LineTop.value = index * 30
}
//#endregion
//#region 搜索 
const searchValue = ref('')
function search(e) {
  const searchUrl = 'https://www.baidu.com/s?wd=' + searchValue.value
  window.open(searchUrl, '_blank');
}
//#endregion

// ElNotification({
//   title: '提示',
//   type: 'info',
//   message: `该页面正在开发中,目前展示内容仅供测试使用`,
//   dangerouslyUseHTMLString: true,
//   duration: 0,
// })

</script>

<style lang="less" scoped>
.main {
  height: 100vh;
  background-color: white;

  .el-container {
    height: 100%;

    .el-header {
      height: 60px;
      padding: 0 40px;
      border-bottom: 1px solid #dcdfe6;

      .header {
        height: 100%;
        display: flex;
        align-items: center;
        justify-content: space-between;

        .header_left {
          display: flex;
          align-items: center;

          .logoBox {
            height: 100%;
            display: flex;
            align-items: center;
            cursor: pointer;
            transition: all 1s;

            &:hover {
              h1 {
                color: #409EFF;
              }
            }

            img {
              width: 40px;
              height: 40px;
            }

            h1 {
              margin: 0;
              margin-left: 10px;
              font-size: 20px;
              color: #303133;
              transition: all 1s;
            }
          }

          .timeBox {
            margin-left: 20px;

            b {
              font-size: 12px;
            }
          }
        }

        .header_right {
          button {
            background-color: #409EFF;
            color: white;
            border: none;
            padding: 5px 10px;
            border-radius: 20px;
            font-size: 14px;
            cursor: pointer;

            &:hover,
            &:active {
              background-color: #398ee2;
            }
          }
        }
      }

    }

    .el-container {
      height: 100%;
      background-color: #f5f5f5;

      .menu {
        position: relative;
        overflow: hidden;
        padding: 20px 40px;

        .menuBoxBar {
          position: absolute;
          width: 3px;
          height: 15px;
          margin: 7.5px 0;
          background-color: #409EFF;
          border-radius: 20px;
          transition: all .5s ease-in-out;
          left: 37px;
        }

        .menuBox {
          line-height: 30px;
          cursor: pointer;
          transition: all .8s;

          &:hover {
            color: #409EFF;
          }
        }

        .active {
          transform: translateX(10px);
          color: #409EFF;
        }
      }

      .container {
        .container_box {
          background-color: white;
          border-radius: 10px;
          padding: 10px 20px;
          box-shadow: 0px 0px 20px -5px rgba(158, 158, 158, .2);
          overflow: hidden;
          margin-bottom: 20px;
        }

        .search {
          height: 400px;
          background-image: url('../assets/img/findView.jpg');
          background-position: center 70%;
          background-size: cover;
          background-repeat: no-repeat;
          position: relative;


          .search_box {
            width: 100%;
            position: absolute;
            z-index: 999;
            top: 100px;
            left: 50%;
            transform: translateX(-50%);
            max-width: 800px;


            // border: 1px solid ;
            .header {
              h1 {
                text-align: center;
                color: white;
                font-weight: normal;
              }
            }


            .body {
              width: 95%;
              background-color: rgba(0, 0, 0, .5);
              border-radius: 50px;
              position: relative;
              backdrop-filter: blur(18px);
              margin: 0 auto;

              input {
                padding: 9px 20px;
                background-color: transparent;
                border: none;
                outline: none;
                // line-height: ;
                height: 50px;
                width: 100%;
                color: white;

                &::-webkit-input-placeholder {
                  color: #909399;
                }
              }

              button {
                position: absolute;
                right: 20px;
                top: 50%;
                transform: translateY(-50%);
                background-color: transparent;
                border: none;
                color: white;
                font-size: 20px;
                font-weight: bold;
                cursor: pointer;

              }
            }

          }

          &::after {
            width: 100%;
            height: 100%;
            content: '';
            background-color: rgba(0, 0, 0, .1);
            position: absolute;
            top: 0;
            left: 0;
          }
        }
      }
    }

  }

}

/*当页面最大宽小于1920 */
@media screen and (max-width: 1920px) {}

/*当页面最大宽小于400*/
@media only screen and (max-width: 450px) {}
</style>